"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import MenuOverlay from "./MenuOverlay";

const TEL = "02-516-7588";

/**
 * ULINE 스타일 헤더: 상단 고정, 스크롤 시 배경 변경
 */
export default function Header() {
  const [scrolled, setScrolled] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 50);
    window.addEventListener("scroll", onScroll, { passive: true });
    onScroll();
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  useEffect(() => {
    if (menuOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "";
    }
    return () => {
      document.body.style.overflow = "";
    };
  }, [menuOpen]);

  return (
    <>
      <header
        className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
          scrolled ? "bg-white shadow-md" : "bg-transparent"
        }`}
      >
        <div className="max-w-7xl mx-auto px-6">
          <div className="flex items-center justify-between h-20 md:h-24">
            {/* 로고 */}
            <Link href="/" className="flex-shrink-0">
              <span className={`text-2xl font-light transition-colors ${scrolled ? "text-[#1a1a1a]" : "text-white"}`}>
                유라인클리닉
              </span>
            </Link>

            {/* 데스크톱 메뉴 */}
            <nav className="hidden lg:flex items-center gap-8">
              <Link href="#" className={`text-sm font-light transition-colors ${scrolled ? "text-[#1a1a1a] hover:text-[#666]" : "text-white/90 hover:text-white"}`}>
                병원 소개
              </Link>
              <Link href="#" className={`text-sm font-light transition-colors ${scrolled ? "text-[#1a1a1a] hover:text-[#666]" : "text-white/90 hover:text-white"}`}>
                시그니처
              </Link>
              <Link href="#" className={`text-sm font-light transition-colors ${scrolled ? "text-[#1a1a1a] hover:text-[#666]" : "text-white/90 hover:text-white"}`}>
                페이스 컷주사
              </Link>
              <Link href="#" className={`text-sm font-light transition-colors ${scrolled ? "text-[#1a1a1a] hover:text-[#666]" : "text-white/90 hover:text-white"}`}>
                바디 컷주사
              </Link>
              <Link href="#" className={`text-sm font-light transition-colors ${scrolled ? "text-[#1a1a1a] hover:text-[#666]" : "text-white/90 hover:text-white"}`}>
                리프팅
              </Link>
              <Link href="#" className={`text-sm font-light transition-colors ${scrolled ? "text-[#1a1a1a] hover:text-[#666]" : "text-white/90 hover:text-white"}`}>
                전후사진
              </Link>
            </nav>

            {/* 우측 버튼들 */}
            <div className="flex items-center gap-4">
              <a
                href={`tel:${TEL.replace(/-/g, "")}`}
                className={`hidden md:flex items-center gap-2 px-4 py-2 rounded-sm font-medium text-sm transition-all duration-300 ${
                  scrolled
                    ? "bg-[#1a1a1a] text-white hover:bg-[#2a2a2a]"
                    : "bg-white/10 text-white hover:bg-white/20 backdrop-blur-sm"
                }`}
              >
                <PhoneIcon />
                전화상담
              </a>
              <button
                type="button"
                className={`lg:hidden flex flex-col justify-center gap-1.5 w-6 h-6 ${scrolled ? "text-[#1a1a1a]" : "text-white"}`}
                onClick={() => setMenuOpen(true)}
                aria-label="메뉴 열기"
              >
                <span className={`block h-px w-full transition-all ${scrolled ? "bg-[#1a1a1a]" : "bg-white"}`} />
                <span className={`block h-px w-full transition-all ${scrolled ? "bg-[#1a1a1a]" : "bg-white"}`} />
                <span className={`block h-px w-full transition-all ${scrolled ? "bg-[#1a1a1a]" : "bg-white"}`} />
              </button>
            </div>
          </div>
        </div>
      </header>

      <MenuOverlay open={menuOpen} onClose={() => setMenuOpen(false)} />
    </>
  );
}

function PhoneIcon() {
  return (
    <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor" aria-hidden>
      <path d="M6.62 10.79c1.44 2.83 3.76 5.14 6.59 6.59l2.2-2.2c.27-.27.67-.36 1.02-.24 1.12.37 2.33.57 3.57.57.55 0 1 .45 1 1V20c0 .55-.45 1-1 1-9.39 0-17-7.61-17-17 0-.55.45-1 1-1h3.5c.55 0 1 .45 1 1 0 1.25.2 2.45.57 3.57.11.35.03.74-.25 1.02l-2.2 2.2z" />
    </svg>
  );
}
